// src/components/home/HeroSection.jsx
"use client";

import { useEffect, useRef } from 'react';
import { motion } from 'framer-motion';
import Link from 'next/link';

export default function HeroSection() {
  // Parallax effect for background
  const backgroundRef = useRef(null);
  
  useEffect(() => {
    const handleScroll = () => {
      const scrollY = window.scrollY;
      if (backgroundRef.current) {
        backgroundRef.current.style.transform = `translateY(${scrollY * 0.2}px)`;
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <section className="relative min-h-screen flex items-center justify-center overflow-hidden bg-gradient-to-br from-[#180031] via-[#34006c] to-[#4d0098] text-white">
      {/* Purple flowing background with gradients */}
      <div
        ref={backgroundRef}
        className="absolute inset-0 z-0"
        style={{
          background: "url(/images/purple-flow-bg.svg) no-repeat center center",
          backgroundSize: "cover",
          opacity: 0.5,
        }}
      ></div>

      {/* Content container */}
      <div className="container mx-auto px-6 z-10 text-center py-24">
        {/* Badge - Similar to "Innovative AI Tool to Streamline" */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          className="inline-block px-4 py-2 rounded-full bg-white/10 backdrop-blur-sm mb-8"
        >
          <span className="font-medium">
            Innovative Blockchain Event of 2025
          </span>
        </motion.div>

        {/* Main headline - The large text */}
        <motion.h1
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.3 }}
          className="text-4xl sm:text-6xl md:text-7xl lg:text-8xl font-bold mb-8 leading-tight"
        >
          The best set
          <br />
          <span className="text-[#b79eff]">of blockchain</span> for you
        </motion.h1>

        {/* Subtitle paragraph */}
        <motion.p
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.6 }}
          className="text-lg md:text-xl max-w-2xl mx-auto mb-12 text-gray-300"
        >
          BlockchainDisrupt is a pioneering technology conference dedicated to
          transforming industries through innovative blockchain solutions.
        </motion.p>

        {/* Action buttons */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.9 }}
          className="flex flex-wrap justify-center gap-4"
        >
          <Link
            href="/tickets"
            className="px-8 py-3 bg-gradient-to-r from-purple-500 to-pink-500 rounded-full text-lg font-medium hover:from-purple-600 hover:to-pink-600 transition-all duration-300"
          >
            Book Tickets
          </Link>
          <Link
            href="/apply-to-partner"
            className="px-8 py-3 bg-transparent border border-white/30 rounded-full text-lg font-medium hover:bg-white/10 transition-all duration-300"
          >
            Become Sponsor
          </Link>
        </motion.div>
      </div>

      {/* Floating orb/sphere at the bottom */}
      <div className="absolute bottom-0 left-1/2 transform -translate-x-1/2 translate-y-1/4 w-96 h-96 md:w-[500px] md:h-[500px] z-0">
        <motion.div
          initial={{ opacity: 0, scale: 0.8 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ duration: 1.5, delay: 1 }}
          className="w-full h-full rounded-full"
          style={{
            background:
              "linear-gradient(135deg, rgba(130, 94, 251, 0.8), rgba(196, 113, 237, 0.8), rgba(246, 215, 255, 0.9))",
            boxShadow: "0 0 100px 20px rgba(124, 58, 237, 0.5)",
            overflow: "hidden",
          }}
        >
          <div
            className="w-full h-full"
            style={{
              background: "url(/images/sphere.png) repeat",
              backgroundSize: "cover",
              mixBlendMode: "overlay",
            }}
          ></div>
        </motion.div>
      </div>
    </section>
  );
}